    -webkit-box-reflect:below 2px linear-gradient(transparent,transparent,#0006);
}
.activeSlide{
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
    transition-timing-function: linear;
    opacity:1;

    
}
